<template>
	<div class="videoPlay">
		<div ref="videoPlay"></div>
		
		<input type="text" v-model.trim="torrent" placeholder="manget:"/>
		<button  @click="setBlobUrl"></button>
	</div>
</template>

<script>
	import 'DPlayer/dist/DPlayer.min.css';
	import DPlayer from 'DPlayer';
	import WebTorrent from 'webtorrent';
	export default {
		data(){
			return {
				torrent: '',
				options: {
				    container: '',
				    autoplay: false,
				    theme: '#FADFA3',
				    loop: true,
				    lang: 'zh-cn',
				    screenshot: true,
				    hotkey: true,
				    preload: 'auto',
				    logo: 'logo.png',
				    volume: 0.7,
				    mutex: true,
				    video: {
				    	url: '',
//				        url: 'http://1251883823.vod2.myqcloud.com/6b94ca32vodsh1251883823/d744a6895285890782304152843/mjQcm1ClBSQA.mp4',
				        pic: 'dplayer.png',
				        thumbnails: 'thumbnails.jpg',
				        type: 'auto'
				    },
				    subtitle: {
				        url: 'dplayer.vtt',
				        type: 'webvtt',
				        fontSize: '25px',
				        bottom: '10%',
				        color: '#b7daff'
				    },
				    danmaku: {
				        id: '9E2E3368B56CDBB4',
//				        api: 'https://api.prprpr.me/dplayer/',
				        token: 'tokendemo',
				        maximum: 1000,
//				        addition: ['https://api.prprpr.me/dplayer/v3/bilibili?aid=4157142'],
				        user: 'DIYgod',
				        bottom: '15%',
				        unlimited: true
				    },
				    contextmenu: [
				        {
				            text: 'custom1',
//				            link: 'https://github.com/DIYgod/DPlayer'
				        },
				        {
				            text: 'custom2',
				            click: (player) => {
				                console.log(player);
				            }
				        }
				    ],
				    highlight: [
				        {
				            text: 'marker for 20s',
				            time: 20
				        },
				        {
				            text: 'marker for 2mins',
				            time: 120
				        }
				    ]
				}
			}
		},
		mounted(){
//			this.setBlobUrl();
		},
		methods:{
			setBlobUrl(){
				var announceList = [
					["udp://tracker.leechers-paradise.org:6969"],
					["udp://tracker.coppersurfer.tk:6969"],
					["udp://tracker.opentrackr.org:1337"],
					["udp://explodie.org:6969"],
					["udp://tracker.empire-js.us:1337"],
					["wss://tracker.btorrent.xyz"],
					["wss://tracker.openwebtorrent.com"],
					["wss://tracker.fastcast.nz"]
				]
				
				global.WEBTORRENT_ANNOUNCE = announceList
				.map(function (arr) {
					return arr[0]
				})
				.filter(function (url) {
					return url.indexOf('wss://') === 0 || url.indexOf('ws://') === 0
				});
//				console.log(global)
		
		var client = new WebTorrent();
		let _this = this;
		
		client.on('error', function(err) {
			console.error('ERROR: ' + err.message)
		})
		// Download by form input
			var torrentId = this.torrent
			if (torrentId.length > 0){
				downloadTorrent(torrentId)
			}
		
		function downloadTorrent(torrentId) {
			console.log('Downloading torrent from ' + torrentId)
			client.add(torrentId, onTorrent)
		}
		
		function onTorrent(torrent) {
			torrent.on('warning', console.log)
			torrent.on('error', console.log)
		
			console.log('Got torrent metadata!')
			console.log(torrent.torrentFileBlobURL)
			var file = torrent.files.find(function (file) {
		    	return file.name.endsWith('.mp4')
		 	});
			console.log(file)
		 	if(file && file._torrent.urlList.length > 0){
			 	let url = file._torrent.urlList[0] +file.path;
					console.log(url)
				_this.options.container = _this.$refs.videoPlay;
				_this.options.video.url = url;
				const dp = new DPlayer(_this.options);
	      	}
		 }
			
			}
		}
	}
</script>

<style>
</style>


