<template>
    <div class="home">
    	<van-swipe class="swiper" ref="vanSwipe" @change="swipeChange" :loop="false" :show-indicators="false" :duration="300">
		  	<van-swipe-item v-for="item in hotsList">
		  		<div class="swiper_box">
		  			<div class="background_img" v-lazy:background-image="item.cover" alt="" />
		  			<div class="">
		  				<p>{{item.title}}</p>
		  				<span></span> <span>{{item.newest_ep_index}}</span>
		  			</div>
		  		</div>
		  	</van-swipe-item>
		</van-swipe>
    	<van-tabs v-model="active" swipeable class="van_tabs" sticky @click="activeTitle">
		  <van-tab v-for="(item,keys) in acgList.activeList" v-model="active" :title="item.navtitle" :key="keys">
		  	<van-list
  				class="van_swiper" 
			  	v-model="loading"
			  	finished="finished"
			  	@load="onLoad">
			  	<van-cell
			  		@click="jumpDetails(pro)"
				    v-for="(pro,index) in item.data"
				    :key="index">
					    <div class="van_flex">
					  		<div class="van_img">
					  			<img v-lazy="pro.imageUrl" alt="" />
					  		</div>
					  		<div class="van_title">
					  			<p>{{pro.animeTitle}}</p>
					  			<div>
					  				<span></span>
					  				<van-rate v-model="pro.rating / 2" class="van_rate"></van-rate>
					  				<!--<van-button round :type="!programTime(pro.timeCN)?'default':'danger'">{{ pro.timeCN != ''?pro.timeCN.substr(0,2)+':'+pro.timeCN.substr(2,2):'' }}</van-button>-->
					  			</div>
					  		</div>
					  	</div>
			  	</van-cell>
			</van-list>
		  </van-tab>	
		</van-tabs>
		
        <!--1
         http://m.imomoe.com/
        dzhan https://www.5dm.tv/
        szhan http://www.silisili.co/	

        bzhan 
        https://bangumi.bilibili.com/jsonp/season_rank_list/global/3.ver?callback=bangumiRankCallback

        
        http://net.huanmusic.com/d_201804251803-->
    </div>
</template>
<script>
	import { programApi } from '../../services/programApi.js';
	import { programJson } from '../../api/program.js'
	import { mapState, mapActions } from 'vuex';
	export default {
		data(){
			return {
				active: 0,
				current: 0,
				acgList: {
					weekList: [],
					activeList:[
						{
							navtitle: '',
							code:'hots',
							data: []
						},
						{
							navtitle: '',
							code: 1,
							data: []
						},
						{
							navtitle: '',
							code: 2,
							data: []
						},
						{
							navtitle: '',
							code: 3,
							data: []
						},
						{
							navtitle: '',
							code: 4,
							data: []
						},
						{
							navtitle: '',
							code: 5,
							data: []
						},
						{
							navtitle: '',
							code: 6,
							data: []
						},
						{
							navtitle: '',
							code: 0,
							data: []
						}
					],
				},
				hotsList: [],
				bannerList: [],
				loading: true,
      			finished: true
			}
		},
		watch:{

		},
		computed: {
			...mapState(["acgObj", "weekObj"])
		},
		filters: {
			
		},
		mounted(){
			//
//			this.getacgList();
			
//			this.getProgramList();
//			//
			this.gethotsList();
			
//			this.getbannerList();
			//
			let day = new Date().getDay();
			this.active = day == 0 ?7:day;
			this.getshin();
			this.activeTitle();
		},
		methods:{
			//
			formatDateTime(unix) {
		        var now = new Date(parseInt(unix) * 1);
		        now =  now.toLocaleString().replace(/|/g, "-").replace(//g, " ");
		        if(now.indexOf("") > 0) {
		            if (now.length == 18) {
		                var temp1 = now.substring(0, now.indexOf(""));   //2014/7/6
		                var temp2 = now.substring(now.indexOf("") + 2, now.length);  // 5:17:43
		                var temp3 = temp2.substring(0, 1);    //  5
		                var temp4 = parseInt(temp3); // 5
		                temp4 = 12 + temp4;  // 17
		                var temp5 = temp4 + temp2.substring(1, temp2.length); // 17:17:43
		                now = temp1 + temp5; // 2014/7/6 17:17:43
		                now = now.replace("/", "-"); //  2014-7/6 17:17:43
		                now = now.replace("/", "-"); //  2014-7-6 17:17:43
		            }else {
		                var temp1 = now.substring(0, now.indexOf(""));   //2014/7/6
		                var temp2 = now.substring(now.indexOf("") + 2, now.length);  // 5:17:43
		                var temp3 = temp2.substring(0, 2);    //  5
		                if (temp3 == 12){
		                    temp3 -= 12;
		                }
		                var temp4 = parseInt(temp3); // 5
		                temp4 = 12 + temp4;  // 17
		                var temp5 = temp4 + temp2.substring(2, temp2.length); // 17:17:43
		                now = temp1 + temp5; // 2014/7/6 17:17:43
		                now = now.replace("/", "-"); //  2014-7/6 17:17:43
		                now = now.replace("/", "-"); //  2014-7-6 17:17:43
		            }
		        }else {
		            var temp1 = now.substring(0,now.indexOf(""));   //2014/7/6
		            var temp2 = now.substring(now.indexOf("")+2,now.length);  // 5:17:43
		            var temp3 = temp2.substring(0,1);    //  5
		            var index = 1;
		            var temp4 = parseInt(temp3); // 5
		            if(temp4 == 0 ) {   //  00
		                temp4 = "0"+temp4;
		            }else if(temp4 == 1) {  // 10  11  12
		                index = 2;
		                var tempIndex = temp2.substring(1,2);
		                if(tempIndex != ":") {
		                    temp4 = temp4 + "" + tempIndex;
		                }else { // 01
		                    temp4 = "0"+temp4;
		                }
		            }else {  // 02 03 ... 09
		                temp4 = "0"+temp4;
		            }
		            var temp5 = temp4 + temp2.substring(index,temp2.length); // 07:17:43
		            now = temp1 + temp5; // 2014/7/6 07:17:43
		            now = now.replace("/","-"); //  2014-7/6 07:17:43
		            now = now.replace("/","-"); //  2014-7-6 07:17:43
		        }
		        return now;
		    },
		    //
		    insertionSortfunction (x, y) {
			    if (x < y) {
			        return -1;
			    }
			    if (x > y) {
			        return 1;
			    }
			    return 0;
			},
			programTime(time){
				if(time != ""){
					let newDate = new Date(this.formatDateTime((new Date().getTime())).substr(0,11)+ time.substr(0,2)+':'+time.substr(2,2)).getTime();
					return new Date().getTime() > newDate;
				}else{
					return false
				}
				
			},
			...mapActions(["setAcgJson", "setWeekJson"]),
			getacgList(){
				let storeAcg = typeof this.acgObj == 'object'?JSON.stringify(this.acgObj):this.acgObj;
				let storeWeek = typeof this.weekObj == 'object'?JSON.stringify(this.weekObj):this.weekObj;
				let quarter = 12 / 4;
				let getFullYear = new Date().getFullYear();
				let getMonth = new Date().getMonth() + 1;
				let getDay = new Date().getDay();
				let bodys = {};
				if(storeAcg != "{}"){
					this.acgList = Object.assign(this.acgList, 
						JSON.parse(storeAcg));
//					console.log(this.acgList.data);
					for(let year in this.acgList.data){
						if(year == getFullYear){
							for(let month in this.acgList.data[year]){
								if(getMonth - month >= 0 && getMonth - month < quarter){
									if(storeWeek != "{}"){
										this.acgList = Object.assign(this.acgList, 
											{
												weekList: JSON.parse(storeWeek)
											});
										console.log(this.acgList.weekList);
										for(let week in this.acgList.weekList){
											for(let active = 0; active < this.acgList.activeList.length; active ++ ){
//												this.acgList.activeList[active].data = [];
												if(this.acgList.weekList[week].weekDayCN == this.acgList.activeList[active].code){
													this.acgList.weekList[week]["code"] = week;
													for(let img = 0; img< this.imgList.length; img ++ ){
														if(this.acgList.weekList[week].bgmId == this.imgList[img].split("/")[this.imgList[img].split("/").length - 1].split("_")[0])
														this.acgList.weekList[week]["imgprc"] = this.imgList[img]
													}
//													imgList
													this.acgList.activeList[active].data.push(this.acgList.weekList[week]);
													this.acgList.activeList[active].data.sort((x, y)=> {
//														if(x.timeCN != ''){
																if (Number(x.timeCN) < Number(y.timeCN)) {
														        return -1;
														    }
														    if (Number(x.timeCN) > Number(y.timeCN)) {
														        return 1;
														    }
//														}
													    return 0;
													})
												}
												if(this.acgList.activeList[active].code == 'hots' && this.acgList.weekList[week].newBgm){
													this.acgList.activeList[active].data.push(this.acgList.weekList[week])
													this.acgList.activeList[active].data.sort((x, y)=> {
//														if(x.timeCN != ''){
																if (Number(x.timeCN) < Number(y.timeCN)) {
														        return -1;
														    }
														    if (Number(x.timeCN) > Number(y.timeCN)) {
														        return 1;
														    }
//														}
													    return 0;
													})
												}
											}
											
										}
									}else{
										bodys.href = this.acgList.data[year][month].path;
//										console.log(bodys)
										programApi.setQuarter(bodys).then( res => {
											this.setWeekJson(res)
										});
									}
									
								}
							}
						}
					}
				}else{
					let bodys = {
						__t: new Date().getTime()
					}
					programApi.getAcgList(bodys).then( res => {
						this.setAcgJson(res);
					});
				};
			},
			activeTitle(){
				for(let i = 0; i <this.acgList.activeList.length; i++){
					if(i == this.active && i != 0 && this.acgList.activeList[i].navtitle.length < 2){
						this.acgList.activeList[i].navtitle = ''+this.acgList.activeList[i].navtitle;
					}
					if(i != this.active && this.acgList.activeList[i].navtitle.length > 1 && i != 0){
						this.acgList.activeList[i].navtitle = this.acgList.activeList[i].navtitle.substr(1)
					}
				}
			},
			onLoad() {
		      // 
//		      setTimeout(() => {
//		        for (let i = 0; i < 10; i++) {
//		          this.list.push(this.list.length + 1);
//		        }
//		        // 
//		        this.loading = false;
//		
//		        // 
//		        if (this.list.length >= 40) {
//		          this.finished = true;
//		        }
//		      }, 500);
		    },
			swipeChange(index){
//				this.active = index;
				this.current = index;
			},
			getProgramList(){
				programApi.getProgramList().then( res => {
					
				});
				let programList = programJson.result;
				programList.forEach((item)=>{
					this.activeList.forEach((active)=>{
						if(item.weekday == active.code){
							active.data.push(item);
						}
					})
					
				})
			},
			gethotsList(){
				programApi.gethotsList().then( res => {
					if(res.message == 'success' && res.result.list.length > 0){
						this.hotsList = res.result.list;
					}
				});
			},
			getbannerList(){
				programApi.banner().then( res => {
					if(res.success && res.banners.length > 0){
						this.bannerList = res.banners;
					}
				});
			},
			getshin(){
				let bodys = {};
				bodys.filterAdultContent = false;
				programApi.shin(bodys).then( res => {
					if(res.success && res.bangumiList.length > 0){
						this.acgList.weekList = res.bangumiList;
						for(let active = 0; active < this.acgList.activeList.length; active++ ){
							for(let week = 0; week < this.acgList.weekList.length; week++ ){
								if(this.acgList.activeList[active].code == this.acgList.weekList[week].airDay){
									this.acgList.activeList[active].data.push(this.acgList.weekList[week]);
								}
							}
						}
					}
				});
			},
			jumpDetails(item){
				this.$router.push({
					path: '/acgdetails',
					query: {
						animeId: item.animeId,
						search: item.animeTitle
					}
				})
			}
		}
	}
</script>
<style type="text/css" lang="less">
.home{
	height: 100%;
	/*display: flex;
	flex-direction: column;*/
	.swiper{
		height: 4.2rem;
		.background_img{
			width: 100%;
			height: 4.2rem;
			background-repeat: no-repeat;
			background-size:100% auto;
			background-position: 10% 10%;
		}
		.swiper_box{
			height: 100%;
			position: relative;
			>div:nth-of-type(1){
				position: absolute;
				top: 0;
				left: 0;
			}
			>div:nth-of-type(2){
				>p {
					padding: 0;
					margin: 0.1rem 0;
				}
				width: 100%;
				height: 1rem;
				text-align: left;
				padding-left: .2rem;
				background-color: rgba(0,0,0,0.5);
				color: #fff;
				position: absolute;
				bottom: -0.5rem;
				left: 0;
				transition: 1s;
			}
			>div:nth-of-type(2):hover, >div:nth-of-type(2):active{
				bottom: 0rem;
			}
		}
	}
	.van_swiper{
		flex: 1;
		.van_flex{
			width: 100%;
			overflow: hidden;
			display: flex;
			margin-bottom: .5rem;
			>.van_img{
				width: 2rem;
				height: 2rem;
				border-radius: .1rem;
				overflow: hidden;
				/*float: left;*/
				margin-right: .3rem;
				>img{
					width: 100%;
				}
			}
			.van_title{
				flex: 1;
				width: 100%;
				height: 2rem;
				/*float: right;*/
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				>p{
					overflow:hidden;
					text-overflow: ellipsis;
					display:-webkit-box;
					-webkit-line-clamp:2;
					-webkit-box-orient:vertical;
					margin: 0;
					padding: 0;
					text-align: left;
				}
				>div{
					overflow: hidden;
					span{
						float: left;
					}	
					.van_rate{
						float: left;
						margin-left: .3rem;
						height: .4rem;
						line-height: .4rem;
					}
				}
			}
		}
	}
}
	.van_flexwrap{
		flex-wrap: wrap;
	}
	
</style>


