<template>
    <div class="active">
        <van-tabbar v-model="active" class="van-active">
            <van-tabbar-item info="" @click="goHome">
                <span></span>
                <img
                slot="icon"
                slot-scope="props"
                :src="props.active ? iconhome.active : iconhome.normal">
            </van-tabbar-item>
            <van-tabbar-item info="" @click="goForum">
                <span></span>
                <img
                slot="icon"
                slot-scope="props"
                :src="props.active ? iconforum.active : iconforum.normal">    
            </van-tabbar-item>
            <van-tabbar-item info="" @click="goAdmin">
                <span></span>
                <img
                slot="icon"
                slot-scope="props"
                :src="props.active ? iconadmin.active : iconadmin.normal">    
            </van-tabbar-item>
        </van-tabbar>
    </div>
</template>
<script>
    export default {
        data(){
            return {
                active: 0,
                iconhome: {
                    normal: require('../../static/images/home.png'),
                    active: require('../../static/images/home-active.png')
                },
                iconforum: {
                    normal: require('../../static/images/forum.png'),
                    active: require('../../static/images/forum-active.png')
                },
                iconadmin: {
                    normal: require('../../static/images/admin.png'),
                    active: require('../../static/images/admin-active.png')
                }
            }
        },
        methods: {
            goHome(){
                this.$router.push({
                    path: '/public/home'
                })
            },
            goForum(){
                this.$router.push({
                    path: '/public/forum'
                })
            },
            goAdmin(){
                this.$router.push({
                    path: '/public/admin'
                })
            }
        }
        
    }
</script>
<style lang="less">
    .active{
        width: 100%;
        height: 1rem;
        .van-active{
            .van-tabbar-item{
                .van-tabbar-item__icon{
                    font-size: 24px !important;
                    >img{
                        height: .48rem;
                    }
                }
            }
            
        }
    }
</style>




