// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import fastclick from 'fastclick'
import LazyLoad from 'vue-lazyload'
import '../static/js/flexible.js';
import $ from 'jquery';
import store from './store'
import { Badge, BadgeGroup, Tabbar, TabbarItem,Tab, Tabs,Swipe, SwipeItem, Row, Col, Button, List, Cell, CellGroup, NavBar, Icon, ImagePreview, Popup, Search, Rate, Panel      } from 'vant';
Vue.use(Badge)
Vue.use(BadgeGroup)
Vue.use(Tab)
Vue.use(Tabs)
Vue.use(Tabbar)
Vue.use(TabbarItem)
Vue.use(Swipe)
Vue.use(SwipeItem)
Vue.use(Row)
Vue.use(Col)
Vue.use(Button)
Vue.use(List)
Vue.use(Cell)
Vue.use(CellGroup)
Vue.use(NavBar)
Vue.use(Icon)
Vue.use(ImagePreview)
Vue.use(Popup)
Vue.use(Search)
Vue.use(Rate)
Vue.use(Panel)

Vue.config.productionTip = false

window.addEventListener('DOMContentLoaded',function(){
  fastclick.attach( document.body );
},false );
Vue.use(LazyLoad,{
  loading: 'http://i0.hdslb.com/bfs/bangumi/6bac8b2154b4d249de0c13b6f111959cb7e4b110.jpg',
  error: 'http://i0.hdslb.com/bfs/bangumi/6bac8b2154b4d249de0c13b6f111959cb7e4b110.jpg',
  attempt: 4,
  preLoad: 1.3,
  listenEvents:['scroll','wheel','mousewheel','resize','animationend','transitionend','touchmove'], //vue
});
Vue.prototype.$ = $

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
  store
})



