import {HttpService} from './http';
/**
 * 
 */
//import { sms } from './commonApi.js'
export const programApi = {
  api: {
  	getAcgList: 'https://bgmlist.com/tempapi/archive.json', //
  	setQuarter: '',
  	getvideoUrl: 'https://api.acplay.net/api/v2/bangumi/shin',
    getProgramList: 'https://bangumi.bilibili.com/api/timeline_v2_cn', //B
    gethotsList: 'https://bangumi.bilibili.com/jsonp/season_rank_list/global/3.ver', //
    searchVal: 'http://usr.005.tv/Aftersome/api_sosuoarctype', //B
    
    
    //play
//  
    populartorrents: 'https://api.acplay.net/api/v2/homepage/populartorrents',
    //bannner
    banner: 'https://api.acplay.net/api/v2/homepage/banner',
    //
    shin: 'https://api.acplay.net/api/v2/bangumi/shin',
    //
    bangumi: 'https://api.acplay.net/api/v2/bangumi/'
  },
  populartorrents(){
  	return HttpService.get(this.api.populartorrents, {}, false, false)
  },
  banner(){
  	return HttpService.get(this.api.banner, {}, false, false)
  },
  shin(bodys){
  	return HttpService.get(this.api.shin, bodys, false, false)
  },
  bangumi(bodys){
  	this.api.bangumi+=bodys.animeId;
  	return HttpService.get(this.api.bangumi, bodys, false, false)
  },
  
  getAcgList(bodys){
  	return HttpService.get(this.api.getAcgList, bodys, false, false)
  },
  setQuarter(bodys){
  	this.api.setQuarter = bodys.href;
  	if(this.api.setQuerter != ''){
  		delete bodys.href;
  	}
  	return HttpService.get(this.api.setQuarter, bodys, false, false);
  },
  getvideoUrl(bodys){
  	return HttpService.get(this.api.getvideoUrl, bodys, false, false)
  },
  getProgramList(bodys){
  	return HttpService.get(this.api.getProgramList, {}, false, false)
  },
  gethotsList(){
  	return HttpService.jsonp(this.api.gethotsList, {}, false, false)
  },
  searchVal(bodys){
  	return HttpService.post(this.api.searchVal, bodys, false, false)
  },
}



