<template>
    <div class="active drag">
    	<van-nav-bar :title="headtitle" left-arrow class="active_header" left-arrow @click-right="onClickRight">
    		<van-icon slot="left">
    			<div class="author_img">
		    		<img :src="returnTime < 3?require('../../static/images/bili_v_1539192226287.gif'):require('../../static/images/782429071774533924.jpg')" alt="" @click="getImgChange"/>
		    	</div>
    		</van-icon>
		  <van-icon name="search" slot="right" class="van_edit"/>
		</van-nav-bar>
    	<van-popup v-model="isSearchShow"  position="right" class="van_popup">
    		<div>
			  <van-search
			    v-model.trim="searchData.keywords"
			    placeholder=""
			    show-action
			    @search="onSearch"
			    @cancel="onCancel"/>
			    <van-list
  				class="van_swiper" 
			  	v-model="loading"
			  	finished="finished"
			  	@load="onLoad">
			  	<van-cell
				    v-for="(pro,index) in searchList"
				    :key="index">
					    <div class="van_flex">
					    	<!--{{pro}}-->
					  		<div class="van_img" v-lazy:background-image="pro.suoluetudizhi" :alt="pro.typename">
					  			<!--<img v-lazy=""  />-->
					  		</div>
					  		<div class="van_title">
					  			<p class="van_name">{{pro.typename}}</p>
					  			<div class="van_content">
					  				<span class="van_label">
					  					<span>{{pro.biaoqian}}</span>
					  					<span>{{pro.zhuangtai}}</span>
					  				</span>
					  				<p class="">
					  					{{pro.description}}
					  				</p>
					  			</div>
					  		</div>
					  	</div>
			  	</van-cell>
			</van-list>
			</div>
    	</van-popup>
    	<audio ref="myAudio" class="myAudio">
    		<source  src="../../static/mucis/sister.mp3" type="audio/mpeg" />
    	</audio>
    </div>
</template>
<script>
	import { programApi } from '../services/programApi.js';
    export default {
    	props:["headtitle"],
    	data(){
    		return {
    			returnTime: 3,
    			isSearchShow: false,
    			searchData: {
					keywords: '',
					pagesize: 10,
					page: 0
				},
				searchList: [],
				loading: false,
      			finished: true
    		}
    	},
    	mounted(){
    		
    	},
    	methods:{
    		getImgChange(){
//  			alert(1)
				let timeout;
				clearTimeout(timeout);
    			
    			this.returnTime --;
    			if(this.returnTime == 2){
    				this.$refs.myAudio.play();
    			}
    			if(this.returnTime == 0){
    				this.returnTime = 3;
    				clearTimeout(timeout)
    			}else{
    				timeout = setTimeout(()=>{
		    			this.getImgChange();
	    			},1150);
//	    			this.$refs.myAudio.pause();
    			}
    		},
    		onLoad() {
//  			alert(1)
		      // 
		      setTimeout(() => {
//		        for (let i = 0; i < 10; i++) {
//		          this.list.push(this.list.length + 1);
//		        }
		        // 
//		        this.loading = false;
		
		        // 
//		        if (this.list.length >= 40) {
//		          this.finished = true;
//		        }
		      }, 500);
		    },
    		onSearch(){
    			programApi.searchVal(this.searchData).then( res =>{
//					console.log(res.result)
					this.loading = true;
					if(res.count == 1 && res.result.length > 0){
						this.searchList = res.result;
						this.loading = false;
					}else{
						this.loading = false;
					}
				});
    		},
    		onCancel(){
    			this.isSearchShow = false;
    		},
    		onClickRight(){
//  			alert(1)
				this.isSearchShow = true;
    		}
    	}
    }
</script>
<style lang="less" scoped>
    .active{
        width: 100%;
        height: 1rem;
        overflow: hidden;
        .active_header{
        	height: 100%;
        	line-height: 1rem;
        }
        .van_popup{
        	height: 100%;
        	width: 100%;
        }
        .author_img{
			float: left;
			width: .8rem;
			height: .8rem;
			overflow: hidden;
			border-radius: 50%;
			text-align: center;
			display: flex;
			justify-content: center;
			margin: .1rem .3rem;
			z-index:1000;
			img{
				height: 100%;
			}
			
		}
		.myAudio{
			display: none;
		}
		.van_edit{
			font-size: 24px;
		}
		.van_flex{
			width: 100%;
			overflow: hidden;
			display: flex;
			margin-bottom: .5rem;
			>.van_img{
				width: 2rem;
				height: 2rem;
				border-radius: .1rem;
				overflow: hidden;
			    background-size: cover;
    			background-position: center;
				margin-right: .3rem;
			}
			.van_title{
				flex: 1;
				width: 100%;
				/*height: 2rem;*/
				/*float: right;*/
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				>p{
					line-height: 1.2;
					overflow:hidden;
					text-overflow: ellipsis;
					display:-webkit-box;
					-webkit-line-clamp:2;
					-webkit-box-orient:vertical;
					margin: 0;
					padding: 0;
					text-align: left;
				}
				>div{
					font-size: 12px;
					color: #666;
					.van_label{
						display: block;
						margin: .1rem 0;
						overflow: hidden;
						>span:nth-of-type(1){
							float: left;
						}
						>span:nth-of-type(2){
							color: #000;
							float: right;
						}
					}
					>p{
						line-height: 1.2;
						overflow:hidden;
						text-overflow: ellipsis;
						display:-webkit-box;
						-webkit-line-clamp:2;
						-webkit-box-orient:vertical;
						margin: 0;
						padding: 0;
						text-align: left;
					}
				}
			}
		}
    }
    .drag{
    	background: inherit;
		transition: 1s;
	    &:after{
	        content: "";
	        width:100%;
	        height:100%;
	        position: absolute;
	        left:0;
	        top:0;
	        background: inherit;
	        filter: blur(10px);
	        z-index: -1;
	    }
	}
	
</style>




