<template>
	<div class="acgdetails">
		<v-header ref="vheader" class="vheader" :headtitle="bangumiObj.animeTitle"></v-header>
		<div v-if="JSON.stringify(bangumiObj) != '{}'">
			<van-list
		 	
			class="van_swiper"
		  	v-model="loading"
		  	finished="finished"
		  	@load="onLoad">
		  	<van-cell>
		    <div class="van_flex">
		  		<div class="van_img" v-lazy:background-image="bangumiObj.imageUrl" :alt="bangumiObj.animeTitle"></div>
		  		<div class="van_title">
		  			<p class="van_name">{{bangumiObj.typename}}</p>
			  			<div class="van_content">
			  				<ul class="van_label">
			  					<li>
			  						<span></span><span></span>
			  					</li>
			  					<li>
			  						<span></span><span>{{bangumiObj.episodes[0].airDate}}</span>
			  					</li>
			  					<li>
			  						<span></span><span>{{bangumiObj.typeDescription}}</span>
			  					</li>
			  					<li>
			  						<span></span>
			  						<van-rate v-model="bangumiObj.rating / 2" class="van_rate"></van-rate>
			  					</li>
			  				</ul>
			  				<p class="">
			  					{{bangumiObj.summary}}
			  				</p>
			  			</div>
			  		</div>
		  		</div>	
		  	</van-cell>
		</van-list>
		<div class="van_panel">
			<van-cell-group>
			  <van-cell title="" :value="`:${bangumiObj.episodes.length} `">
			    <van-icon slot="right-icon" name="arrow" class="van-cell__right-icon" @click="isMore = true"/>
			  </van-cell>
			</van-cell-group>
			<div class="van_panel_content">
				<van-button type="default" v-if="keys < 15" v-for="(item,keys) in bangumiObj.episodes">{{keys + 1}}</van-button>
			</div>
		</div>
		</div>

		<van-popup v-model="isMore" position="bottom" :overlay="true" class="van_popup_more">
			<van-cell-group>
			  <van-cell title="">
			  	<van-icon slot="right-icon" class="van-cell__right-icon"  name="close" @click="isMore = false"/>
			  </van-cell>
			</van-cell-group>
			<div class="van_panel_content">
				<van-button type="default" v-for="(item,keys) in bangumiObj.episodes" @click="isMore = false">{{keys + 1}}</van-button>
			</div>
		</van-popup>
	</div>
</template>

<script>
	import { programApi } from '../services/programApi.js';
	import vHeader from './header.vue';
	export default {
		components:{
	    vHeader
	  },
	  data () {
	    return {
		    activeKey: 0,
		    searchData: {
				keywords: '',
				pagesize: 10,
				page: 0
			},
			searchList: [],
			bangumiObj: {},
			loading: false,
			finished: true,
			isMore: false
	    }
	  },
	  mounted(){
	  	document.addEventListener('visibilitychange', function () {
			     var normal_title = '(**)!';
			    if (document.visibilityState == 'hidden') {
			        normal_title = document.title;
			        document.title = '(o)';
			    } else document.title = normal_title;
			});
			let clientHeader = this.$refs.vheader.$el.clientHeight;
			let clientH = window.innerHeight;
	//		console.log(clientH);
			this.$('.v-content').css('height',clientH - clientHeader + 'px');
			
			this.searchData.keywords = this.$route.query.search;
			this.onSearch();
			this.bangumi();
	  },
	  methods: {
	    onClick(keys){
	      this.activeKey = keys
	    },
	    onSearch(){
			programApi.searchVal(this.searchData).then( res =>{
//					console.log(res.result)
				this.loading = true;
				if(res.count == 1 && res.result.length > 0){
					this.searchList = res.result;
					this.loading = false;
				}else{
					this.loading = false;
				}
			});
		},
		bangumi(){
			let bodys = {};
			bodys.animeId = this.$route.query.animeId;
			programApi.bangumi(bodys).then( res => {
				if( res.success && JSON.stringify(res.bangumi) !="{}"){
					this.bangumiObj = res.bangumi;
					console.log(this.bangumiObj )
				}
			});
		},
	    onLoad() {
//  			alert(1)
	      // 
	      setTimeout(() => {
//		        for (let i = 0; i < 10; i++) {
//		          this.list.push(this.list.length + 1);
//		        }
	        // 
//		        this.loading = false;
	
	        // 
//		        if (this.list.length >= 40) {
//		          this.finished = true;
//		        }
	      }, 500);
	    },
	  }
	}
</script>

<style lang="less">
	.acgdetails{
		width: 100%;
		height: 100vh;
		.van_flex{
			width: 100%;
			overflow: hidden;
			display: flex;
			/*align-content: flex-start;*/
			margin-bottom: .5rem;
			>.van_img{
				width: 2rem;
				/*height: 2rem;*/
				border-radius: .1rem;
				overflow: hidden;
			    background-size: 2rem auto;
			    background-repeat: no-repeat;
    			background-position: 20% 8%;
				margin-right: .3rem;
			}
			.van_title{
				flex: 1;
				width: 100%;
				/*height: 2rem;*/
				/*float: right;*/
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				>p{
					line-height: 1.2;
					overflow:hidden;
					text-overflow: ellipsis;
					display:-webkit-box;
					-webkit-line-clamp:2;
					-webkit-box-orient:vertical;
					margin: 0;
					padding: 0;
					text-align: left;
				}
				>div{
					font-size: 12px;
					color: #666;
					.van_label{
						display: block;
						margin: .1rem 0;
						overflow: hidden;
						>span:nth-of-type(1){
							float: left;
						}
						>span:nth-of-type(2){
							color: #000;
							float: right;
						}
						>li:nth-of-type(4){
							overflow: hidden;
							>span{
								float: left;
							}
							>div{
								float: left;
								/*margin-left: .1rem;*/
								height: .4rem;
								line-height: .4rem;
							}
						}
					}
					>p{
						line-height: 1.2;
						/*overflow:hidden;
						text-overflow: ellipsis;
						display:-webkit-box;
						-webkit-line-clamp:2;
						-webkit-box-orient:vertical;
						margin: 0;
						padding: 0;
						text-align: left;*/
					}
				}
			}
		}
		.van_panel{
			.van-cell__title{
				text-align: left;
			}
			.van_panel_content{
				overflow: hidden;
				padding-bottom: .5rem;
				padding-left: .4rem;
				>button{
					width: 1rem;
					height: .6rem;
					line-height: .6rem;
					float: left;
					margin: .5rem .4rem 0 0;
				}
			}
		}
		.van_popup_more{
			height: 50%;
			.van-cell{
				.van-cell__title{
					text-align: left;
				}
			}
			.van_panel_content{
				overflow: hidden;
				padding-bottom: .5rem;
				padding-left: .4rem;
				>button{
					width: 1rem;
					height: .6rem;
					line-height: .6rem;
					float: left;
					margin: .5rem .4rem 0 0;
				}
			}
		}
	}
</style>


