<template>
  <div class="hello">
    <v-header ref="vheader" class="vheader"></v-header>
    <div class="v-content" ref="vcontent">
      <router-view></router-view>
    </div>
    <!-- <van-badge-group :active-key="activeKey">
      <van-badge title="" @click="onClick" />
      <van-badge title="" @click="onClick" info="8" />
      <van-badge title="" @click="onClick" info="99" />
      <van-badge title="" @click="onClick" info="199" />
    </van-badge-group> -->
    <v-active ref="vactive"></v-active>
  </div>
</template>

<script>
import vHeader from './header.vue';
import vActive from './active.vue';
export default {
  name: 'HelloWorld',
  components:{
    vHeader,
    vActive
  },
  data () {
    return {
      activeKey: 0
    }
  },
  mounted(){
  	document.addEventListener('visibilitychange', function () {
		     var normal_title = '(**)!';
		    if (document.visibilityState == 'hidden') {
		        normal_title = document.title;
		        document.title = '(o)';
		    } else document.title = normal_title;
		});
		let clientHeader = this.$refs.vheader.$el.clientHeight;
		let clientActive = this.$refs.vactive.$el.clientHeight;
		let clientH = window.innerHeight;
//		console.log(clientH);
		this.$('.v-content').css('height',clientH - clientHeader - clientActive + 'px')
  },
  methods: {
    onClick(keys){
      this.activeKey = keys
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .hello{
    /*display: flex;
    flex-direction: column;*/
    width: 100%;
    /*height: 100vh;*/
    .v-content{
      /*flex: 1;*/
    }
    .vheader{
    	position: relative;
    	background: rgba(255,255,255,.5)
    }
  }
</style>



